import React, { Component } from "react";
import { Avatar, Row, Image, Card } from "antd";
import { UserOutlined } from "@ant-design/icons";
import { ROW_GUTTER } from "constants/ThemeConstant";
import Flex from "components/shared-components/Flex";

export class IDInfo extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		const { IDCardUrl } = this.props.user;

		return (
			<>
				<Flex
					alignItems="center"
					mobileFlex={false}
					className="text-center text-md-left"
				></Flex>
				<div className="mt-4">
					<Row style={{ width: "100%" }}>
						<Card title="ID Card">
							<Image src={IDCardUrl} />
						</Card>
					</Row>
				</div>
			</>
		);
	}
}

export default IDInfo;
