import React, { Component, setState } from "react";
import axios from "axios";
import {
	Link,
	Route,
	Navigate,
	useLocation,
	Routes,
	useParams,
} from "react-router-dom";
import {
	UserOutlined,
	LockOutlined,
	CreditCardOutlined,
	BellOutlined,
	PictureOutlined,
	SafetyOutlined,
	IdcardOutlined,
} from "@ant-design/icons";
import userData from "assets/data/user-list.data.json";
import { Menu } from "antd";
import InnerAppLayout from "layouts/inner-app-layout";
import ProfileInfo from "./PersonalInfo";
import PaymentInfo from "./PaymentInfo";
import Selfies from "./Selfies";
import Billing from "./Billing";
import IDInfo from "./IDInfo";

const url = "/app/pages/user-detail";

const MenuItem = ({ id, icon, path, label }) => {
	return (
		<>
			{icon}
			<span>{label}</span>
			<Link to={`${url}/${id}/${path}`} />
		</>
	);
};

const SettingOption = () => {
	const location = useLocation();
	const { id } = useParams();

	const locationPath = location.pathname.split("/");

	const currentpath = locationPath[locationPath.length - 1];

	return (
		<Menu
			mode="inline"
			selectedKeys={[currentpath]}
			items={[
				{
					key: "personal-info",
					label: (
						<MenuItem
							id={id}
							label="Personal Info"
							icon={<UserOutlined />}
							path="personal-info"
						/>
					),
				},
				// {
				// 	key: 'change-password',
				// 	label: <MenuItem label="Change Password" icon={<LockOutlined />} path="change-password" />
				// },
				{
					key: "billing",
					label: (
						<MenuItem
							id={id}
							label="Billing"
							icon={<CreditCardOutlined />}
							path="billing"
						/>
					),
				},
				// {
				// 	key: 'notification',
				// 	label: <MenuItem label="Notification" icon={<BellOutlined />} path="notification" />
				// },
				{
					key: "selfies",
					label: (
						<MenuItem
							id={id}
							label="Selfies"
							icon={<PictureOutlined />}
							path="selfies"
						/>
					),
				},
				{
					key: "payment-information",
					label: (
						<MenuItem
							id={id}
							label="Payment Information"
							icon={<CreditCardOutlined />}
							path="payment-information"
						/>
					),
				},
				{
					key: "id-info",
					label: (
						<MenuItem
							id={id}
							label="ID Information"
							icon={<IdcardOutlined />}
							path="id-info"
						/>
					),
				},
				{
					key: "legal-info",
					label: (
						<MenuItem
							id={id}
							label="Legal Information"
							icon={<SafetyOutlined />}
							path="legal-info"
						/>
					),
				},
			]}
		/>
	);
};

const UserDetailWrapper = () => {
	const { id } = useParams();
	return <UserDetail id={id} />;
};

class UserDetail extends Component {
	constructor(props) {
		super(props);
		this.state = { user: "" };
	}

	componentDidMount() {
		axios
			.post("http://localhost:5000/api/management/getUser", {
				id: this.props.id,
			})
			.then((res) => {
				this.setState({
					user: res.data.user,
				});
			});
	}

	render() {
		const SettingContent = () => {
			return (
				<Routes>
					<Route
						path="personal-info"
						element={<ProfileInfo user={this.state.user} />}
					/>
					<Route path="billing" element={<Billing user={this.state.user} />} />
					<Route path="selfies" element={<Selfies user={this.state.user} />} />
					<Route
						path="payment-information"
						element={<PaymentInfo user={this.state.user} />}
					/>
					<Route path="id-info" element={<IDInfo user={this.state.user} />} />
					<Route
						path="legal-info"
						element={<ProfileInfo user={this.state.user} />}
					/>
					<Route path="*" element={<Navigate to="personal-info" replace />} />
				</Routes>
			);
		};

		return (
			<InnerAppLayout
				sideContentWidth={320}
				sideContent={<SettingOption />}
				mainContent={<SettingContent />}
			/>
		);
	}
}

export default UserDetailWrapper;
